import Pow from "../images/pow.png";

function Card({ pokemonData }) {
  if (pokemonData) {
    const pokemonMoves = [];
    for (let i = 0; i < 3; i++) {
      console.log("pokemonData.moves[i]: ", pokemonData.moves[i]);
      if (pokemonData.moves[i]) {
        pokemonMoves.push(pokemonData.moves[i].move.name);
      }
    }

    return (
      <div id="Card">
        <div>
          <span id="title">Name: </span>
          <span id="characterName"> {pokemonData.name}</span>
        </div>
        <img
          src={pokemonData.sprites.front_default}
          alt="Pokemon Pic"
          width="200"
          height="300"
          style={{ margin: "-25px" }}
        />
        <div>
          <span id="title">Type: </span>
          <span>
            {pokemonData.types.map((obj) => {
              let symbol = "";
              switch (obj.type.name) {
                case "normal":
                  symbol = "ü•±";
                  break;
                case "electric":
                  symbol = "‚ö°";
                  break;
                case "ice":
                  symbol = "üßä";
                  break;
                case "psychic":
                  symbol = "üîÆ";
                  break;
                case "grass":
                  symbol = "üå±";
                  break;
                case "poison":
                  symbol = "‚ò†";
                  break;
                case "bug":
                  symbol = "üêõ";
                  break;
                case "fairy":
                  symbol = "üßö‚Äç‚ôÄÔ∏è";
                  break;
                case "fire":
                  symbol = "üî•";
                  break;
                case "rock":
                  symbol = "‚õ∞";
                  break;
                case "water":
                  symbol = "üíß";
                  break;
                case "flying":
                  symbol = "ü¶Ö";
                  break;
                case "ghost":
                  symbol = "üëª";
                  break;
                case "ground":
                  symbol = "üåé";
                  break;
                case "steel":
                  symbol = "ü§ò";
                  break;
                case "fighting":
                  symbol = "ü•ä";
                  break;
                default:
                  break;
              }
              return (
                <div className="typeWrapper">
                  {obj.type.name}
                  <span>{symbol}</span>
                </div>
              );
            })}
          </span>
        </div>
        <div>
          <span id="title">Moves: </span>
          <div id="movesWrapper">
            {pokemonMoves.map((pokemonMove) => {
              const isPunch = pokemonMove.includes("punch");
              console.log(isPunch);
              // condition ? true : false;
              const formattedPokemonMove = isPunch ? (
                <>
                  {pokemonMove}
                  <img src={Pow} />
                </>
              ) : (
                pokemonMove
              );
              return <div className="moveWrapper">{formattedPokemonMove}</div>;
            })}
          </div>
        </div>
      </div>
    );
  }
  return null;
}

export default Card;
